@import '../fonts/fonts-globals.scss';

.main-info {
    font-family: 'Cinzel', serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-weight: 900;
    text-transform: uppercase;
    text-align: center;
    position: absolute;
    top: 30%;
    width: 100%;
    height: 30%;
    font-size: 70px;
    letter-spacing: 10px;
    border-right: 1px solid $color-white;
    border-left: 1px solid $color-white;
    animation: moveup 2s ease;
    transition: 0.2s ease;
    z-index: 5;
    padding: 0 20px;
    span {
        animation: blink-heart 2.5s steps(30, end) infinite;
    }
    div {
        width: 100%;
    }
}
/* Heart blinking effect */
@keyframes blink-heart {
    from, to { color: transparent }
    0%  { color:  rgba(255, 255, 255, 0.0) } 
    25% { color:  rgba(255, 255, 255, 0.50) }
    50% { color:  rgba(255, 255, 255, 1) }
    75% { color: rgba(255, 255, 255, 0.50) }
    100% { color: rgba(255, 255, 255, 0.0) }
}
@keyframes moveup {
    0% {
        top: -100%;
        opacity: 0%;
    }
    25% {
        opacity: 25%;
    }
    50% {
        opacity: 50%;
    }
    75% {
        opacity: 75%;
    }
    100% {
        opacity: 100%;
    }
}

@media (max-width: 992px) {
    .main-info {
        font-size: 60px;
    }
}

@media (max-width: 700px) {
    .main-info {
        font-size: 50px;
        letter-spacing: 5px;
    }
}

@media (max-width: 600px) {
    .main-info {
        font-size: 45px;
    }
}

@media (max-width: 465px) {
    .main-info {
        padding-left: 25px;
        span {
            font-size: 60px;
        }
    } 
}

@media (max-width: 425px) {
    .main-info { 
        font-size: 45px;
        line-height: 55px;
        span {
            display: inline-block;
            line-height: 65px;
            margin-top: 5px;
        }
    }  
}

@media (max-width: 400px) {
    .main-info {
        letter-spacing: 3px;
        font-size: 40px;
    }
}
  