@import '../fonts/fonts-globals.scss';

.about {
    @extend %rubric-styles;
    p {
        text-align: center;
    }
}

@media (max-width: 992px) {
    .about {
        p {
            font-size: 32px;
            line-height: 45px;
        }
    }
}

@media (max-width: 730px) {
    .about {
        p {
            font-size: 25px;
            line-height: 35px;
        }
    }
}