@import '../fonts/fonts-globals.scss';

.contacts {
    @extend %rubric-styles;
    div {
        width: 100%;
        text-align: center;
    }
}


@media (max-width: 992px) {
    .contacts {
        div {
            font-size: 32px;
            line-height: 45px;
        }
    } 
}

@media (max-width: 730px) {
    .contacts {
        div {
            font-size: 32px;
            line-height: 42px;
        }
    }
}

@media (max-width: 375px) {
    .contacts {
        div {
            font-size: 30px;
            line-height: 40px;
        }
    }
}
  
@media (max-width: 340px) {
    .contacts {
        div {
            font-size: 28px;
            line-height: 38px;
        }
    }
}