@import '../fonts/fonts-globals.scss';

.footer {
    margin-top: auto;
    padding: 90px 0;
}
.footer-socials {
    display: flex;
    justify-content: center;
    align-items: center;
    a {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        border: 1px solid $color-white;
        transition: .2s ease;
        
    }
    li:nth-child(1) {
        margin-left: 20px;
    }
    li {
        margin-right: 20px;
    }
    &::before {
        content: "";
        display: block;
        height: 1px;
        background-color: #fff;
        width: 100%;
    }
    &::after {
        content: "";
        display: block;
        height: 1px;
        background-color: #fff;
        width: 100%;
    }
}
.socials-link:hover {
    background-color: $color-white-alpha;
}
.socials-link {
    img {
        width: 25px;
        height: 25px;
    }
}
