@import '../fonts/fonts-globals.scss';

.cv {
    @extend %rubric-styles;
    text-align: center;
}
.cv-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 60px;
    border: 1px solid $color-white;
    border-radius: 5px;
    color: $color-white;
    transition: .2s ease;
    margin-top: 40px;
    font-size: 30px;
    &:hover {
        background-color: $color-white-alpha;
    }
}


@media (max-width: 992px) {
    .cv {
        font-size: 32px;
        line-height: 45px;
        a {
            font-size: 24px;
            width: 240px;
            height: 45px;
        }
    }  
}