@import '../fonts/fonts-globals.scss';

.burger {
    @extend %menu;
}
.burger-items {
    display: flex;
    right: 0;
    top: 150%;
    position: absolute;
    border: 1px solid $color-white;
    border-radius: 5px;
}
.burger-item {
    @extend %menu-item;
    padding: 5px 15px;
    &:nth-child(2) {
        margin: 0 15px;  
    }
}
.burger-link {
    @extend %menu-link;
    display: block;   
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    padding: 0 15px;
    &.active {
        border-left: 1px solid $color-white;
        border-right: 1px solid $color-white;
    }
}
.burger-wrapper {
    margin-left: auto;
    position: relative;
    display: flex;
    img { 
        width: 26px;
        height: 26px;
        margin-left: auto;
        display: block;
    }
}

@media (min-width: 701px) {
    .burger-wrapper {
        display: none;
    }  
}

@media (max-width: 700px) {
    .burger-items {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .burger-wrapper {
        display: block;
    }
    .burger-item:nth-child(2) {
        margin: 0;
    }
}